const fetchGeneros = async (filtro = "movie") => {
  const tipo = filtro === "movie" ? "movie" : "tv";

  const url = `https://api.themoviedb.org/3/genre/${tipo}/list?api_key=bb7834ea1345a2cd972e6508f044a2a8&language=es-MX&page=1`;

  try {
    const respuesta = await fetch(url);
    const datos = await respuesta.json();
    return datos.genres;
  } catch (error) {
    console.log(error);
  }
};
export default fetchGeneros;
